import React, { Component } from 'react'
import styles from './App.module.css'
import { Navigation, Heading, Page0, Page1} from './components'
// import { createRGBcode, sampleintLessThan } from './utils'
const criminals=[
  { key:0, name:'0', 
    where:'지하철역 출구 계단', 
    who:'치마 입은 여성', 
    what:'치마 속 신체 부위', 
    how:'동영상으로 1회', 
    tool:'휴대전화', 
    imgurl:'data:image/jpeg;base64,'  
  },
  { key:1, name:'1', 
    where:'지하철 환승통로 에스컬레이터', 
    who:'연령불상', what:'다리 부위', 
    how:'사진과 동영상으로 50회' , 
    tool:'휴대전화', 
    imgurl:'https://speckyboy.com/wp-content/uploads/2014/06/ironman.jpg'  
  },
  { key:2, name:'2', 
    where:'모텔', 
    who:'연인 관계인 여성', 
    what:'성관계 하는 장면', 
    how:'동의를 받지 않은 채' , 
    tool:'휴대전화', 
    imgurl:'https://d13ezvd6yrslxm.cloudfront.net/wp/wp-content/images/Mike-Mitchell-Bullseye.jpg'   },
  { key:3, name:'3', 
    where:'여성 화장실', 
    who:'화장실에 들어온 여성들', 
    what:'용변 보는 장면', 
    how:'몰래 설치해 놓은 뒤' , 
    tool:'소형 카메라', 
    imgurl:'http://cdn.collider.com/wp-content/uploads/Deadpool-Mike-Mitchell-Mondo-Marvel.jpg'  
  },
  { key:4, name:'4', 
    where:'자신의 거주지', 
    who:'여자친구', 
    what:'나체 사진', 
    how:'친구들과 함께 있는 메신저채팅방에 공유' , 
    tool:'휴대전화', 
    imgurl:'https://inveteratemediajunkies.files.wordpress.com/2014/04/marvel-portraits-doctor-strange-by-mike-mitchell.jpg?w=640'  
  },
]
class App extends Component {
  state = { 
    criminals: criminals, 
    selectedIndex: null,
    selectedSex:null,
    selectedAge:null
  }

  selectSex = (event) => {
    const { name } = event.target
    this.setState({selectedSex:Number(name)})
  }
  
  selectAge = (event) => {
    const { name } = event.target
    this.setState({selectedAge:Number(name)})
  }

  selectCriminal = (event) => {
    const { name } = event.target
    this.setState({selectedIndex:Number(name)})
  }

  setNextStage = () => {
    this.setState({
      criminals:criminals,  
      selectedIndex: null,
    })
  }

  setInitialStage = () => {
    this.setState({
      criminals:criminals, 
      selectedIndex: null,
    })
  } 

  render() {
    const { criminals, selectedIndex, selectedSex, selectedAge }=this.state
    return (
      <div className={styles.wrapper}>
        <Navigation />
        <Heading />
        {selectedIndex == null && (
        <Page0 
            selectCriminal={this.selectCriminal}
            criminals={criminals} 
            selectedIndex={selectedIndex}
            selectedSex={selectedSex}
            selectedAge={selectedAge}
            selectSex={this.selectSex}
            selectAge={this.selectAge}
        />
        )}
        {selectedIndex!== null && (
          <Page1
            criminals={criminals}
            selectedIndex={selectedIndex}
            setInitialStage={this.setInitialStage}
            selectedSex={selectedSex}
            selectedAge={selectedAge}
          />     
        )}
      </div>
    )
  }
}

export default App
